#wrapper {
  padding-top: 60px;
  padding-bottom: 190px;
  min-height: 100%;
  box-sizing: border-box;
  position: relative;
}

#wrapper.bad-see {
  padding-top: 120px;
}

#wrapper.bad-see .container {
  width: 100%;
}

#wrapper.bad-see .fixed-header {
  top: 60px;
}

#wrapper.bad-see .fixed-header .sidemenu {
  max-height: calc(100vh - 120px);
}

#wrapper.bad-see .main .sidebar .scroll-block {
  top: 120px;
  max-height: calc(100vh - 120px);
}

.fixed-header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 60px;
  background-color: #47b1c8;
}

.fixed-header .container {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}

.fixed-header .container .search-form {
  position: relative;
  display: flex;
  max-width: calc(100% - 50px);
}

.fixed-header .container .search-form input {
  font-size: 0.8125rem;
  font-weight: 400;
  background: #ffffff;
  border: none;
  width: 411px;
  height: 30px;
  border-radius: 15px;
  box-sizing: border-box;
  padding: 0 30px 0 18px;
  max-width: 100%;
}

.fixed-header .container .search-form button {
  cursor: pointer;
  display: block;
  width: 30px;
  height: 30px;
  background: url("../img/search.svg") left center no-repeat;
  position: absolute;
  right: 0;
  top: 0;
  border: none;
}

.fixed-header .container .low_see {
  font-size: 1rem;
  color: #ffffff;
  position: relative;
  line-height: 1;
}

.fixed-header .container .low_see::before {
  content: "";
  display: inline-block;
  width: 22px;
  height: 16px;
  background: url("../img/see.svg") center center no-repeat;
  margin-right: 9px;
  margin-bottom: 3px;
  vertical-align: middle;
}

.fixed-header .container .mobile-icons {
  display: none;
  align-items: center;
  margin-right: -11px;
}

.fixed-header .container .mobile-icons .search-btn {
  display: block;
  width: 19px;
  height: 19px;
  background: url("../img/search-mobile.svg") center center no-repeat;
  background-size: contain;
  cursor: pointer;
  transition: transform 400ms;
}

.fixed-header .container .mobile-icons .search-btn.active {
  transform: rotate(-45deg);
}

.fixed-header .container .mobile-icons .mobile-menu-toggle {
  display: block;
  cursor: pointer;
}

.fixed-header .container .mobile-icons .mobile-menu-toggle svg {
  width: 50px;
  height: 50px;
  display: block;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.fixed-header .container .mobile-icons .mobile-menu-toggle svg .line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #ffffff;
  stroke-width: 5;
  stroke-linecap: round;
}

.fixed-header .container .mobile-icons .mobile-menu-toggle svg .line.top {
  stroke-dasharray: 40 121;
}

.fixed-header .container .mobile-icons .mobile-menu-toggle svg .line.bottom {
  stroke-dasharray: 40 121;
}

.fixed-header .container .mobile-icons .mobile-menu-toggle.active svg {
  transform: rotate(45deg);
}

.fixed-header .container .mobile-icons .mobile-menu-toggle.active svg .line.top {
  stroke-dashoffset: -68px;
}

.fixed-header .container .mobile-icons .mobile-menu-toggle.active svg .line.bottom {
  stroke-dashoffset: -68px;
}

.fixed-header .sidemenu {
  position: absolute;
  left: 0;
  top: 100%;
  background: #ffffff;
  display: none;
  max-height: calc(100vh - 60px);
  overflow-y: auto;
  overflow-x: hidden;
  border-bottom: 4px solid #47b1c8;
}

.fixed-header .sidemenu .menu {
  border-radius: 0;
  border: none;
}

