.sidemenu {
  width: 100%;
}

.sidemenu .burder {
  background: #47b1c8;
  color: #fff;
  font-size: 1rem;
  width: 100%;
  border-top: 1px solid #e2e7ea;
  line-height: 40px !important;
  height: 40px;
  padding-left: 12px;
  box-sizing: border-box;
  position: sticky;
  top: 0;
  z-index: 10;
}

.sidemenu .burder::before {
  content: "";
  display: inline-block;
  margin-right: 13px;
  width: 18px;
  height: 10px;
  background: url("../img/burger.svg") center center no-repeat;
  background-size: contain;
}

.sidemenu ul {
  list-style: none;
}

.sidemenu .menu {
  border-radius: 0 0 5px 5px;
  border: 1px solid #e6e6e6;
  border-top: none;
  width: 100%;
  padding: 11px 0 25px;
  box-sizing: border-box;
}

.sidemenu .menu > li > a {
  margin-right: 40px;
  margin-left: 30px;
}

.sidemenu .menu > li > ul {
  padding: 5px 0;
}

.sidemenu .menu > li > ul > li > a {
  margin-left: 50px;
}

.sidemenu .menu > li > ul > li > ul {
  padding: 5px 0;
}

.sidemenu .menu > li > ul > li > ul > li > a {
  margin-left: 70px;
  color: #7e8894;
}

.sidemenu .menu li {
  padding-top: 6px;
  padding-bottom: 6px;
  position: relative;
}

.sidemenu .menu li a {
  color: #474950;
  font-size: 0.9375rem;
  line-height: 1.125rem;
  position: relative;
  display: inline-block;
}

.sidemenu .menu li.active::before {
  content: "";
  display: block;
  height: 100%;
  width: 2px;
  background: #da2238;
  position: absolute;
  left: 0;
  top: 0;
}

.sidemenu .menu li.active > a {
  color: #da2238;
}

.sidemenu .menu li.has-child > a::before {
  content: "";
  display: block;
  width: 5px;
  height: 10px;
  background: url("../img/menu-arr.svg") center center no-repeat;
  background-size: contain;
  position: absolute;
  left: -15px;
  top: 4px;
}

.sidemenu .menu li.has-child ul {
  display: none;
}

.sidemenu .menu li.has-child.opened {
  border-bottom: 1px solid #e6e6e6;
}

.sidemenu .menu li.has-child.opened > a::before {
  transform: rotate(90deg);
}

.sidemenu .menu li.has-child.opened > ul {
  display: block;
  border-top: 1px solid #e6e6e6;
  margin-top: 12px;
}

